<template>
    <view class="copy">
        <view class="copy-label" @click="handleEdit">
            <i class="w_editor-icon w_editor-icon-fuzhiwenjian"></i>
            <text>{{data.label}}</text>
        </view>
        <view class="copy-value">
            <view class="copy-value__info" @click="handleEdit">
                {{data.value}}
            </view>
            <view class="copy-value__btn" @click.stop="copy">
                一键复制
            </view>
        </view>
        <WPopup :show="show" @confirm="handleConfirm" @cancel="cancel">
            <template slot="title">
                添加复制区域
            </template>
            <view class="copy-form">
                <span class="copy-form__label">描述</span>
                <u-input v-model="data.label" type="text" placeholder="例如: 复制后搜索添加微信"/>
                <span class="copy-form__label">需要被复制内容</span>
                <u-input v-model="data.value" type="text" placeholder="可输入微信号, 公众号等"/>
            </view>
            <u-toast ref="uToast"></u-toast>
        </WPopup>
    </view>
</template>

<script>
    import WPopup from '../components/w_popup/index.vue'
    export default {
        components: {
            WPopup
        },
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
            readOnly: {
                type: Boolean,
                default: false
            },
        },
        data() {
            return {
				data: {},
                show: false,
                opened: false
            }
        },
        mounted() {
            const val = this.value || {}
            if(!this.readOnly && !val.value) {
                this.show = true
            }else{
                this.opened = true
            }
        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                immediate: true,
                handler(e) {
                    if(e){
                        this.data = e
                    }else{
                        this.data = {
                            value: '',
                            label: ''
                        }
                    }
                },
            },
        },
        methods: {
            cancel() {
                this.show = false
                if(!this.opened){
                    this.$emit('permit', false)
                }
            },
            handleEdit() {
                if(!this.readOnly){
                    this.show = true
                }
            },
            handleConfirm() {
                if(this.data.value){
                    this.show = false
                    this.opened = true
                    this.$emit('permit', true)
                }else{
                    this.$refs.uToast.show({message: '被复制内容不能为空'})
                }
            },
            copy() {
                uni.setClipboardData({
                	data: this.data.value,
                	success: function () {
                		console.log('success');
                	}
                });
            },
        }
    }
</script>

